* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    vertical-align: top;
}

body {
    font-size: 14px;
    font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
    line-height: 1.5em;
    background: #fafbfc;
    color: #333;
    line-height: 26px;
}

h3 {
    font-size: 1.2em;
    margin-bottom: 10px;
}

.validate {
    padding: 8px 0;
}

p {
    font-size: 14px;
}

p.heading {
    margin-top: 20px;
    font-size: 15px;
    font-weight: 700;
    padding-bottom: 0;
}

p code,
li code {
    line-height: 2.1
}

header {
    width: 100%;
    background-color: #fff;
    z-index: 1;
    border-bottom: 1px solid #f0f0f0;
}

nav {
    margin: 0 auto 0;
    padding: 0 15px;
    color: #353535;
    font-size: 13px;
    max-width: 1100px;
}

.spacer {
    flex: 1;
}

.toolbar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #e1644d;
    color: white;
    font-weight: 600;
}

.toolbar img {
    margin: 0 16px;
}

.sub-title,
tagline-title {
    font-weight: 400
}

nav:after {
    content: "";
    display: table;
    clear: both;
}

nav {
    margin: 0 auto 0;
    padding: 0 15px;
    color: #353535;
    font-size: 13px;
    max-width: 1100px;
}

.dev-tools-logo {
    width: 30px;
    height: 30px;
    padding: 9px 0 0 3px;
    float: left;
    background: #1E8BC3;
    border-radius: 2px;
    text-align: center;
    margin: 4px 0 0;
    font-weight: 700;
    font-size: 9px;
    letter-spacing: 0.2px;
    color: #fff;
}

.logo {
    display: block;
    padding: 6px 15px 0;
    float: left;
}

.logo h1 {
    font-size: 13px;
    font-weight: 700;
    float: left;
    margin: 0;
}

.sub-title,
tagline-title {
    font-weight: 400
}

nav:after {
    content: "";
    display: table;
    clear: both;
}

.toggle,
[id^=drop] {
    display: none;
}

nav ul {
    float: right;
    margin: 0;
    list-style: none;
    position: relative;
    padding: 0;
    z-index: 101;
}

nav ul li {
    margin: 0;
    display: inline-block;
    background-color: #fafafa;
    padding: 7px 0;
    border-left: 1px solid #f0f0f0;
    border-right: 1px solid #f0f0f0;
}

nav a {
    display: block;
    padding: 0 15px;
    color: #333;
    text-decoration: none;
}

nav ul li ul li:hover {
    background: #f4f4f4;
}

nav ul ul {
    display: none;
    position: absolute;
    top: 40px;
    right: 0;
    border-top: 1px solid #f0f0f0;
}

nav ul li:hover>ul {
    display: inherit;
}

nav ul ul li {
    width: 240px;
    float: none;
    display: list-item;
    position: relative;
    border-bottom: 1px solid #f0f0f0;
    padding: 7px 0;
    line-height: 20px;
}

nav ul ul li.jscompress {
    border-left: 5px solid #F8DC3C;
}

nav ul ul ul li {
    position: relative;
    top: -60px;
    left: 300px;
}

.three-lines {
    position: relative;
    display: inline-block;
    width: 0.8em;
    height: 0.8em;
    margin: 7px 10px 0 0;
    border-top: 0.1em solid #333;
    border-bottom: 0.1em solid #333;
}

.three-lines:before {
    content: "";
    position: absolute;
    top: 0.3em;
    left: 0;
    width: 100%;
    border-top: 0.1em solid #333;
}

.hide {
    display: none;
}

.container {
    display: block;
    margin: 0 auto;
    max-width: 1100px;
    padding: 0 15px;
    text-align: left;
}

footer {
    text-align: center;
    max-width: 1100px;
    padding: 15px;
    margin: 0 auto 0;
    border-top: 1px solid #eee;
    font-size: 12px;
    font-weight: 700;
}

main {
    display: block;
    width: 100%;
    max-width: 950px;
    margin: 0 auto;
    padding: 1.5em 0;
}

.CodeMirror-gutters {
    /* something gone wrong with gutter after update, setting its min height manually */
    min-height: 400px !important;
    min-height: calc(100vh - 350px) !important;
}

.CodeMirror-dialog input {
    vertical-align: middle;
}

#code,
.CodeMirror {
    width: 100%;
    height: 380px;
    height: calc(100vh - 440px);
    min-height: 380px;
    border: 1px solid #e1e1e1;
    margin-bottom: 1em;
    font-size: 14px;
}

.line-error {
    background-color: #FBE3E4;
}

button[type="submit"] {
    background-color: #fff;
    padding: 10px 15px;
    text-decoration: none;
    border: 1px solid #e1e1e1;
    font-size: 14px;
    font-family: Open Sans, sans-serif;
    font-weight: 700;
    color: #333;
    margin: 10px 10px 0 0;
}

button[type="submit"]:hover {
    background: #f3f3f3;
    border: 1px solid #d1d1d1;
    cursor: pointer;
}

button[type="submit"]:active {}

button[type="reset"],
button.copy {
    background-color: #f0f0f0;
    padding: 10px 15px;
    text-decoration: none;
    border: 1px solid #ddd;
    font-size: 14px;
    font-family: Open Sans, sans-serif;
    font-weight: 700;
    color: #333;
    margin-top: 10px;
}

button[type="reset"]:hover {
    cursor: pointer;
    background: #ddd;
    border: 1px solid #ccc;
}

button[type="reset"]:active {}

button.copy {
    float: right;
}

@media all and (min-width: 541px) {
    button.copy {
        display: none;
    }
}

@media (max-width: 775px) {
    .more-dev {
        display: none;
    }
}

#result-container {
    display: none;
    padding-top: 15px;
}

#result-container.shown {
    display: block;
}

#result-container h3 {
    font-size: 1.3em;
}

#result {
    border: 1px solid transparent;
    margin: 1.6em 0;
    padding: 0.8em;
}

#result:empty {
    display: none;
}

#result.error {
    background: #FBE3E4;
    color: #D12F19;
    border-color: #FBC2C4;
}

#result.success {
    background: #E6EFC2;
    color: #529214;
    border-color: #C6D880;
}

footer {
    padding-bottom: 3.5em !important;
}